.root {
  background-color: #cff9fe;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.open_btn {
  position: absolute;
  width: 175px;
  height: 45px;
  font-size: 14px;
  border-radius: 50px;
  color: #ffffff;
  background-color: #9e77ed;
  box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05), 0px 0px 0px 4px #f4ebff;
  border: 1px solid #7f56d9;
  font-weight: 600;
  line-height: 20px;

  cursor: pointer;
  svg {
    margin-left: 5px;
    font-size: 16px;
  }
  &:active {
    transform: translateY(2px);
  }
}

.container {
  width: 400px;
  height: 620px;
  margin: 50px auto;
  background-color: #fff;
  border-radius: 30px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
  padding: 40px;
  transform: translate(0%, 30%);
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
  transition: all 0.5s ease-in-out;
  transition-delay: 0.2s;
  opacity: 0;
  visibility: hidden;

  &.show {
    opacity: 1;
    visibility: visible;
    transform: translate(0%, 0%);
  }
}

.close_icon {
  width: 18px;
  opacity: 0.3;
  transform: rotate(45deg);
  margin: -20px 0px 10px 320px;
  cursor: pointer;
  &:hover {
    opacity: 1;
  }
}

.search {
  position: relative;

  input {
    border: 1px solid rgba(0, 0, 0, 0.08);
    padding: 15px;
    border-radius: 15px;
    width: 100%;
    font-size: 16px;
    padding-left: 50px;
  }

  svg {
    width: 18px;
    height: 18px;
    position: absolute;
    top: 17px;
    left: 20px;
    opacity: 0.3;
  }
}

.users_list {
  list-style: none;
  padding: 0;
  margin: 0;
  overflow: auto;
  height: calc(100% - 130px);
  margin-top: 30px;

  li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;

    & > div {
      display: flex;
    }

    img.avatar {
      border-radius: 30px;
      width: 50px;
      height: 50px;
      margin-right: 15px;
    }

    .action {
      width: 28px;
      height: 28px;
      opacity: 0.3;
      cursor: pointer;

      &:hover {
        opacity: 0.8;
      }
    }

    h3 {
      margin: 0;
      font-weight: 900;
      font-family: 'Merriweather', serif;
      margin-left: 15px;
      margin-top: 5px;
      margin-left: 0;
      color: #182170;
    }

    p {
      display: flex;
      align-items: center;
      font-size: 14px;
      margin: 0;
      color: rgba(0, 0, 0, 0.5);
      margin-top: 3px;

      svg {
        width: 16px;
        height: 16px;
        margin-right: 4px;

        path {
          fill: rgba(0, 0, 0, 0.2);
        }
      }
    }
  }
}

.send_invite_btn {
  background-color: #7f56d9;
  padding: 16px 15px;
  border-radius: 15px;
  font-size: 16px;
  color: #fff;
  width: 100%;
  border: 0;
  outline: none;
  box-shadow: 0 7px 10px #7f56d96c;
  cursor: pointer;
  transition: all 0.1s ease-in-out;

  &:hover {
    background-color: #9e77ed;
  }

  &:active {
    transform: translateY(2px);
    box-shadow: none;
  }
}

.success_block {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  flex-direction: column;
  animation: success-show 0.5s cubic-bezier(0.25, 0.75, 0.5, 1.25);

  img {
    width: 120px;
    height: 120px;
  }

  h3 {
    font-size: 24px;
    margin-bottom: 0;
    margin-top: 10px;
  }

  p {
    text-align: center;
    margin-bottom: 30px;
  }

  button {
    width: 200px;
  }
}

.skeleton_list {
  height: calc(100% - 130px);
  margin-top: 30px;

  svg {
    margin-bottom: 15px;
  }
}

@keyframes success-show {
  0% {
    opacity: 0;
    transform: scale(0.8);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}
